"use client";
import { useState, useEffect } from "react";

const mockAssets = [
	{ symbol: "ETH", address: "0x0000000000000000000000000000000000000003" },
	{ symbol: "BTC", address: "0x0000000000000000000000000000000000000004" },
];

let nextPositionId = 1;

export default function TradePage() {
	const [asset, setAsset] = useState(mockAssets[0].address);
	const [size, setSize] = useState("");
	const [leverage, setLeverage] = useState(1);
	const [isLong, setIsLong] = useState(true);
	const [entryPrice, setEntryPrice] = useState(2000); // mock price
	const [margin, setMargin] = useState("");
	const [positions, setPositions] = useState<any[]>([]);
	const [message, setMessage] = useState("");

	useEffect(() => {
		const link = document.createElement("link");
		link.href =
			"https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap";
		link.rel = "stylesheet";
		document.head.appendChild(link);
		return () => {
			document.head.removeChild(link);
		};
	}, []);

	function handleOpenPosition() {
		if (!size || !margin || isNaN(Number(size)) || isNaN(Number(margin))) {
			setMessage("Enter valid size and margin");
			return;
		}
		const pos = {
			id: nextPositionId++,
			asset,
			size: Number(size),
			entryPrice,
			margin: Number(margin),
			leverage,
			isLong,
			isOpen: true,
		};
		setPositions((p) => [...p, pos]);
		setMessage(
			`Opened ${isLong ? "Long" : "Short"} position on ${getSymbol(asset)}`
		);
		setSize("");
		setMargin("");
	}

	function handleClosePosition(id: number) {
		setPositions((p) =>
			p.map((pos) =>
				pos.id === id
					? { ...pos, isOpen: false, closePrice: entryPrice }
					: pos
			)
		);
		setMessage("Closed position");
	}

	function getSymbol(address: string) {
		return (
			mockAssets.find((a) => a.address === address)?.symbol || "?"
		);
	}

	return (
		<div className="centered-container">
			<div className="max-w-xl w-full">
				<h1 className="text-3xl font-bold mb-4 shadow-title">Trade</h1>
				<p className="text-gray-600 mb-6">
					Open or close leveraged positions on supported asset pairs.
				</p>
				<div className="card mb-6">
					<div className="mb-4">
						<label className="block mb-1 font-medium">Asset</label>
						<select
							className="w-full"
							value={asset}
							onChange={(e) => setAsset(e.target.value)}
						>
							{mockAssets.map((a) => (
								<option key={a.address} value={a.address}>
									{a.symbol}
								</option>
							))}
						</select>
					</div>
					<div className="mb-4 flex gap-4">
						<div className="flex-1">
							<label className="block mb-1 font-medium">Size</label>
							<input
								type="number"
								className="w-full"
								value={size}
								onChange={(e) => setSize(e.target.value)}
								min={0}
							/>
						</div>
						<div className="flex-1">
							<label className="block mb-1 font-medium">Margin</label>
							<input
								type="number"
								className="w-full"
								value={margin}
								onChange={(e) => setMargin(e.target.value)}
								min={0}
							/>
						</div>
					</div>
					<div className="mb-4 flex gap-4">
						<div className="flex-1">
							<label className="block mb-1 font-medium">Leverage</label>
							<input
								type="number"
								className="w-full"
								value={leverage}
								onChange={(e) => setLeverage(Number(e.target.value))}
								min={1}
								max={100}
							/>
						</div>
						<div className="flex-1 flex items-end gap-2">
							<button
								className={
									isLong
										? "bg-[#6366f1] text-white"
										: "bg-[#f1f5f9] text-[#1a202c]"
								}
								onClick={() => setIsLong(true)}
							>
								Long
							</button>
							<button
								className={
									!isLong
										? "bg-[#6366f1] text-white"
										: "bg-[#f1f5f9] text-[#1a202c]"
								}
								onClick={() => setIsLong(false)}
							>
								Short
							</button>
						</div>
					</div>
					<button
						className="w-full"
						onClick={handleOpenPosition}
					>
						Open Position
					</button>
					{message && (
						<div className="mt-4 text-[#6366f1] font-semibold">
							{message}
						</div>
					)}
				</div>
				<div className="card">
					<h2 className="text-xl font-semibold mb-2">
						Open Positions
					</h2>
					<table className="w-full text-left">
						<thead>
							<tr>
								<th className="py-1">ID</th>
								<th className="py-1">Asset</th>
								<th className="py-1">Size</th>
								<th className="py-1">Leverage</th>
								<th className="py-1">Type</th>
								<th className="py-1">Status</th>
								<th className="py-1">Action</th>
							</tr>
						</thead>
						<tbody>
							{positions.length === 0 && (
								<tr>
									<td
										colSpan={7}
										className="text-gray-400 py-2 text-center"
									>
										No open positions
									</td>
								</tr>
							)}
							{positions.map((pos) => (
								<tr key={pos.id} className="border-t">
									<td className="py-1">{pos.id}</td>
									<td className="py-1">{getSymbol(pos.asset)}</td>
									<td className="py-1">{pos.size}</td>
									<td className="py-1">{pos.leverage}x</td>
									<td className="py-1">
										{pos.isLong ? "Long" : "Short"}
									</td>
									<td className="py-1">
										{pos.isOpen ? "Open" : "Closed"}
									</td>
									<td className="py-1">
										{pos.isOpen && (
											<button onClick={() => handleClosePosition(pos.id)}>
												Close
											</button>
										)}
										{!pos.isOpen && (
											<span className="text-gray-400">Closed</span>
										)}
									</td>
								</tr>
							))}
						</tbody>
					</table>
				</div>
			</div>
		</div>
	);
}